//api
import {queryUser,
        userProjectRoleQuery,
		userProjectDepartmentQuery,
		departmentQuery,
		projectQuery
	   } from './api';

//
class Login {
	constructor(arg) {
		
	}
	//code
    getCode(){
		return new Promise((resolve,reject)=>{
			uni.login({
				success:(res)=>{
					resolve(res.code)
				},
				fail:(error)=>{
					reject(error)
				}
			})
		})
	}
	
	//UserInfo
	getUserInfo(){
		return new Promise((resolve,reject)=>{
			uni.getUserInfo({
				success:(res)=>{
				 resolve(res)	
				},
				fail:(error)=>{
				  reject(error)
				}
			})
		})
	}
	
	//OpenId
	getOpenId(code){
		return new Promise((resolve,reject)=> {
	       let appId = "wxd763acfcb06de61a";
		   let appSecret = "e8e7ea381fff59c2081d115cb312b22e";
		   uni.request({
		   	 url:`https://api.weixin.qq.com/sns/jscode2session?appid=${appId}&secret=${appSecret}&js_code=${code}&grant_type=authorization_code`,
			 method:"GET",
			 data:{},
			 success:(res)=>{
				 resolve(res)
			 },
			 fail:(error)=>{
				 reject(error)
			 }
		   })
		})
	}
	
	//
	insertUser(userInfo,api,method){
		return new Promise((resolve,reject)=>{
			uni.request({
				url:api,
				method:method,
				data:userInfo,
				success:(res)=>{
					resolve(res.data)
				},
				fail:(error)=>{
					reject(error)
				}
			})
		})
	}
	
}


